.video__wrapper {
  background-image: linear-gradient(to top, #ffffff 12%, #000000 10%);
}

.video {
  position: relative;
  height: 100%;
  padding-bottom: 76px;
}

.video__block h2 {
  font-weight: 600;
  font-size: 29px;
  margin-left: -8px;
  line-height: 30px;
  margin-bottom: 20px;
  color: #ffffff;
  text-align: center;
}

.video__block p {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
  opacity: 0.7;
  color: #ffffff;
  max-width: 340px;
  text-align: center;
  margin-bottom: 54px;
}


.video__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 121px;
}

.video__btn {
  width: 65px;
  height: 65px;
  top: 44%;
  left: 46.5%;
  border-radius: 50%;
  background-color: #0ad9c6;
  z-index: 1000;
  cursor: pointer;
  position: absolute;

  &::after {
    content: "";
    position: absolute;
    width: 13px;
    height: 18.2px;
    top: 39.5%;
    left: 41.5%;
    border: 9px solid transparent;
    border-left: 14px solid #000000;
    z-index: 1000;
    cursor: pointer;
  }

  &:hover {
    background-color: gray;
  }

  &:hover::after {
    border: 9px solid transparent;
    border-left: 14px solid #ffffff;
  }

  &:focus::before {
    background-color: gray;
  }

  &:focus::after {
    border: 9px solid transparent;
    border-left: 14px solid #ffffff;
  }
}

.video__content-block {
  position: relative;
  tab-index: 0;

  img {
    position: relative;
  }
}

