@media (max-width: $tablet-width) {
  .video__block h2 {
    font-size: 23px;
    line-height: 30px;
    padding-top: 0;
    margin: 0;
    margin-bottom: 21px;
    margin-left: 3px;
  }

  .video__block {
    padding-top: 69px;
  }

  .video__block p {
    font-size: 16px;
    line-height: 28px;
    max-width: 430px;
    margin-bottom: 39px;
  }

  .video__content-block img {
    width: 503px;
    height: 282px;
  }

  .video__btn {
    width: 90px;
    height: 90px;
    top: 34%;
    left: 41%;

    &::after {
      top: 39%;
      left: 47%;
      border: 11px solid transparent;
      border-left: 16px solid #000000;
    }
  }


  .video__wrapper {
    background-image: linear-gradient(to top, #ffffff 17%, #000000 10%);
  }

  .video {
    padding-bottom: 33px;
  }
}

@media (max-width: $mobile-width) {
  .video__content-block img {
    width: 288px;
    height: 161.66px;
  }

  .video__btn {
    left: 39%;
    top: 29.5%;
    width: 65px;
    height: 65px;

    &::after {
      top: 38.5%;
      left: 45.5%;
      border: 9px solid transparent;
      border-left: 14px solid #000000;
    }
  }

  .video__block p {
    max-width: none;
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 51px;
  }

  .video__block {
    padding-top: 83px;
  }

  .video__block h2 {
    margin: 0 0 21px -9px;
  }

  .video__wrapper {
    background-image: linear-gradient(to top, #ffffff 11%, #000000 10%);
  }
}
